import '@/public/css/login/common.less';
import md5 from '../../public/js/md5.js';
export default {
	data() {
		return {
			Register: true,
			account: '',
			msgCaptcha: '',
			show: true,
			timer: null,
			count: ''
		}
	},
	onLoad() {
		this.changeCaptcha();
	},
	onHide(){
		clearInterval(this.timer);
	},
	methods: {
		// 注册
		commitRegister() {
			// let cookie = uni.getStorageSync('cookieKey');
			// let config = {
			//     headers:{}
			// };
			// if(cookie){
			//     config.headers.Cookie = cookie;
			// }
			if (this.account != '' && this.msgCaptcha != '') {
				this.$http.post('/Login/register', {
					account: this.account,
					msgCaptcha: this.msgCaptcha,
				}).then(res => {
					if (res.code != 1) {
						uni.showToast({
							icon: 'none',
							title: res.message
						})
					} else {
						let token = 'Bearer ' + res.data.token;
						uni.setStorageSync('token', token);
						uni.setStorageSync('account',this.account); 
						uni.showToast({
							icon: 'success',
							title: res.message
						})
						setTimeout(() => {
							uni.navigateTo({
								url: 'setPassword'
							})
						}, 1000)
					}
				}).catch(err => {
					uni.showToast({
						icon: 'none',
						title: '系统错误，请稍后再试!'
					})
				})
			} else {
				uni.showToast({
					icon: 'none',
					title: '请将信息填写完整！'
				})
			}
		},
		// 去登录页面
		goLogin() {
			uni.navigateTo({
				url: './login'
			})
		},
		// 获取手机验证码
		getCode() {
			if (this.account == '') {
				uni.showToast({
					title: '请输入手机号码！',
					icon: 'none'
				})
			} else {
				this.$http.get('/Login/captcha', {
					account: this.account,
					type: 2
				}).then(res => {
					const TIME_COUNT = 60;
					if (!this.timer) {
						this.count = TIME_COUNT;
						this.show = false;
						this.timer = setInterval(() => {
							if (this.count > 0 && this.count <= TIME_COUNT) {
								this.count--;
							} else {
								this.show = true;
								clearInterval(this.timer);
								this.timer = null;
							}
						}, 1000)
					}
					if (res.code === 1) {

					} else {
						uni.showToast({
							icon: 'none',
							title: res.message
						})
					}
				})
			}
		}
	}
}
